.top-template-options-container {
    -moz-border-bottom-colors: none !important;
    -moz-border-left-colors: none !important;
    -moz-border-right-colors: none !important;
    -moz-border-top-colors: none !important;
    background: none repeat scroll 0 0 #3a3b3d;
    border-bottom: 3px solid #0289d4;
    height: auto !important;
    padding: 9px 10px 7px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 999;
    .project-actions {
        background: none repeat scroll 0 0 transparent !important;
        box-shadow: none !important;
        float: left;
        padding: 0;
        z-index: 9;
        width: 640px;

        .preview-project {
            float: right;
        }
    }
    .ui.fluid.input {
        > {
            label {
                color: #fff;
                float: left;
                font-size: 17px;
                font-weight: 600;
                margin-left: 105px;
                padding: 4px 20px 6px;
                border-left: 1px solid #fff;
            }
            input {
                float: left;
                padding: 5px;
                width: 280px;
            }
        }
        width: auto;
    }
    .small-back {
        float: left;
    }
    .ui.bottom.attached {
        &.segment.aligned.center {
            padding: 0;
            width: 610px;
            float: right;
            background: none repeat scroll 0 0 transparent !important;
            border: 0 none !important;
            box-shadow: none !important;
        }
        &.aligned > a {
            margin-left: 8px;
        }
    }
    .small-back {
        color: #fff;
    }
}

.setting-trigger {
    -webkit-transition: all 0.1s ease-in-out;
    -moz-transition: all 0.1s ease-in-out;
    -o-transition: all 0.1s ease-in-out;
    transition: all 0.1s ease-in-out;
    background: none repeat scroll 0 0 #3a3b3d;
    cursor: pointer;
    float: left;
    font-weight: bold;
    padding: 5px 10px;
    position: absolute;
    text-align: center;
    width: 100px;
    z-index: 1;
    color: #fff !important;
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ie-user-select: none;
    i.fa-bars {
        position: relative;
        top: 1px;
    }
    &.active-sidebar-trigger {
        i.fa-bars {
            display: none;
        }
        > .fa-plus-circle {
            display: block;
            -ms-transform: rotate(45deg);
            /* IE 9 */
            -webkit-transform: rotate(45deg);
            /* Chrome, Safari, Opera */
            transform: rotate(45deg);
        }
    }
    i {
        margin: 0 !important;
        padding: 0 !important;
        color: #fff !important;
        font-size: 21px;
        float: left;
    }
    .fa-plus-circle {
        display: none;
        color: #0289D4;
        font-size: 21px;
        padding: 1px 0 !important;
    }
}

.body--video-only {
    #project-preview {
        margin-top: 5%;
    }
}

.sidebar--media-library {
    //top: 55px !important;
    //padding-bottom: 55px !important;
    padding: 20px;
}

.mce-container {
    box-sizing: content-box;

    & * {
        box-sizing: content-box;
    }
}

#project-options {
    position: relative;
}

.option {
