@import "bourbon/bourbon";
@import "editor";

.modal {
    &.modal--vpa {
        & > .header {
            background: #028EDD;
            color: #FFF;
            border-top-left-radius: inherit;
            border-top-right-radius: inherit;
            margin: -1px -1px 0 -1px;
            border-bottom: 10px solid #0284D8;
        }

        & > .content {
            border-top: 10px solid #DAEDFB;
            background-color: #FFF;
            padding-top: 1.5em;
            padding-bottom: 0;
        }
    }

    &.modal--project-publish {
        .list__wp-sites {
            max-height: 150px;
            min-height: 20px;
            overflow-y: auto;
            overflow-x: hidden;

            i.icon {
                vertical-align: top;
            }
        }
    }
    select {    
    background-color: #ffffff;
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-radius: 0.3125em;
    box-shadow: 0 0 rgba(0, 0, 0, 0.3) inset;
    box-sizing: border-box;
    color: rgba(0, 0, 0, 0.7);
    font-size: 1em;
    margin: 0;
    outline: 0 none;
    padding: 0.65em 1em;
    transition: background-color 0.3s ease-out 0s, box-shadow 0.2s ease 0s, border-color 0.2s ease 0s;
}

.item {
    min-height: 0 !important;
}

.clonePageModal .header, .addNewVideoModal .header, .addNewPageModal .header, .addNewProjectModal .header, .addNewConversionModal .header, .addNewEITabModal .header, .addNewEIModal .header {
    background-color: #66b34e;
    background-image: url("//f.cl.ly/items/3E0t1M2Q2l2p0l2J0301/gridme-light.png");
    border-bottom: 2px solid rgba(0, 0, 0, 0.2);
    color: #ffffff;
}

.clonePageModal .step, .addNewPageModal .step, .addNewVideoModal .step, .addNewConversionModal .step {
    text-align: center;
}

.clonePageModal .step, .addNewPageModal .step, .addNewVideoModal .step, .addNewConversionModal .step {
    background-color: #34393c !important;
    border-bottom: 2px solid rgba(0, 0, 0, 0.2);
    color: #ffffff !important;
}

.addNewPageModal .inline.field,
.clonePageModal  .inline.field {
    margin: 0 !important;
}
}

.body--video-only {
    #wrapper {
        background: #f6f6f6 url('../images/checked_bg.png') repeat;

        #main {
            position: absolute;
            top: 50%;
            margin: -259px auto 0 auto;
        }
    }
}

.project-editor {
    &#main {
        margin: 0;
        padding: 53px 0 0 0;
    }

    #content {
        //margin-top: 46px;
        position: relative;
    }

    &.project-editor--sales-page {
        iframe {
            width: 100%;
            height: 100%;
        }
    }

    &.project-editor--video-only {
        #content {
            text-align: center;
        }
        iframe {
            margin: 0 auto;
        }
    }

    #sidebar {
        top: 53px;
        padding-bottom: 53px;
    }

}

